@import url(https://fonts.googleapis.com/css2?family=Ibarra+Real+Nova&family=Lato&display=swap);
:root {
  --primary-700: #1DA1F2;
  --primary-400: #68bdf2;
  --secondary-700: #F4B400;
  --secondary-400: #f0cd6d;
  --light-400: #bfcadd;
  --light-700: #d0def5;
  --light-900: #ffffff;
  --dark-400: #36393e;
  --dark-700: #282b30;
  --dark-900: #1e2124;
}




body {
  margin: 0;
  font-family: 'Ibarra Real Nova', serif;
font-family: 'Lato', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background: #ffffff;
  background: var(--light-900);
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

p {
  margin: 0;
}

a {
  color: inherit;
  text-decoration: none;
  cursor: pointer;
}

*, *::before, *::after {
  box-sizing: border-box;
  -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
  -webkit-tap-highlight-color: transparent;
  -webkit-touch-callout: none;
}

button {
  cursor: pointer;
  display: inline-block;
  margin: 0;
  border: none;
  font-family: inherit;
  font-size: 1rem;
  outline: none;  
  border-radius: 4px;
  padding: 0.85rem 1.5rem;
  border-radius: 1.5rem;
}


input {
  background: #ffffff;
  background: var(--light-900);
}


input:-webkit-autofill,
input:-webkit-autofill:hover, 
input:-webkit-autofill:focus, 
input:-webkit-autofill:active{
    -webkit-text-fill-color: #282b30 !important;
    -webkit-text-fill-color: var(--dark-700) !important;
    /* border: 1px solid var(--black) !important; */
    /* -webkit-box-shadow: 0 0 0 30px var(--white) inset !important; */
    -webkit-background-clip: text;
}

textarea {
  background: #ffffff;
  background: var(--light-900);
  color: #1e2124;
  color: var(--dark-900);
}



.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

.centered {
  width: 100%;
  height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;
  background: var(--light-900);
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

